.order {

    &__button {


        background-color: rgba(187, 243, 48, 1);
        border-radius: 30px;

        width: 98px;
        height: 32px;

        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        line-height: 1.5;
    


    }

    &__backdrop {
   
            position: fixed;
            z-index: 6;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(94, 99, 99, 0.7);
            overflow-y: scroll;

            &.is-hidden {
                opacity: 0;
                pointer-events: none;
            }
     
    }

    &__modal {
            position: absolute;
            z-index: 7;
            top: 50%;
            left: 50%;
            transform: scale(1) translate(-50%, -50%);
            width: 320px;
            height: 449px;
            padding: 32px 20px;
            background-color: rgba(255, 229, 80, 1);
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);
            border-radius: 30px;

                @media screen and (min-width: 768px) {
                    width: 458px;
                    padding: 32px 32px;
                }

                @media screen and (max-height: 448px) and (orientation: landscape) {
                    max-height: 95vh;
                    overflow-y: scroll;
                    margin-top: 5px;
                    margin-bottom: 5px;
                }

        
    }

    &__btn-x {

            position: absolute;
            z-index: 8;
            right: 8px;
            top: 8px;
            width: 32x;
            height: 32px;
            padding: 4px;
            border-radius: 50%;
            border: none;
            background-color: rgba(255, 255, 255, 1);
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);

    }

    &__title {
        text-align: center;

        font-weight: 700;
        font-size: 24px;
        line-height: 120%;
    }

    &__form {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
    }

    &__form-set {
        display: flex;
        flex-direction: column;
        margin-top: 24px;

        font-weight: 500;
        font-size: 16px;
        line-height: 140%;

            &:first-child {
                margin-top: 0;
            }
    }

    &__form-name {
        margin-left: 12px;
    }

    &__input {
        padding-left: 12px;
        margin-top: 2px;
        height: 48px;
        box-shadow: inset 0px 2px 4px rgba(0, 0, 0, 0.15);
        border-radius: 30px;

            @media screen and (max-width: 767px) {
                border: 1px solid rgba(126, 177, 1, 1);
            }

            @media screen and (min-width: 768px) {
                border: 1px solid rgba(18, 18, 18, 0.12);
            }

            &::placeholder {
                font-weight: 400;
                font-size: 16px;
                line-height: 20px;
                color: rgba(196, 196, 196, 1);
            }
    }


    &__btn-send {
        margin-left: auto;
        margin-right: auto;
        width: 106px;
        height: 40px;
        margin-top: 32px;

        background-color: rgba(255, 255, 255, 1);
        border-radius: 30px;
        border: 2px solid rgba(126, 177, 1, 1);
    
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        line-height: 1.5;

    }
    
}

/*--------------------------------------- HEADER----------------------------------------------------------------*/
.order_button-header {
    @media screen and (max-width: 767px) {
        display: none;
    }
}

/*--------------------------------------- HERO----------------------------------------------------------------*/

.order_button-hero {
    margin-top: 28px;

    @media screen and (min-width: 768px) {
        margin-top: 40px;
    }
}

/*--------------------------------------- Offerings----------------------------------------------------------------*/
.order__button-offerings {
    width: 106px;
    height: 40px;
    background: rgba(255, 255, 255, 1);
    border: 2px solid rgba(126, 177, 1, 1);
    border-radius: 30px;

        @media screen and (max-width: 767px) {
            margin-top: 28px;
        }

        @media screen and (min-width: 768px) {
            margin-top: 40px;
        }

         @media screen and (min-width: 1280px) {
            margin-top: 32px;
         }
}